.option-item {
  color: #272727;
  font-size: 1.1rem;
  font-weight: 350;
  line-height: 1.3;
  position: relative;
}

:root {
  --scrollbar-width: 8px;
  --mask-fade-height: 32px;
}

.masked-overflow {
  padding-right: calc(var(--scrollbar-width)  + 10px);
  padding-bottom: calc(var(--mask-fade-height)  + 10px);
  mask-image: linear-gradient(to top, transparent, black 37%, black calc(100% - var(--mask-fade-height)), transparent), linear-gradient(black, black);
  mask-size: calc(100% - var(--scrollbar-width)) 100%, var(--scrollbar-width) 100%;
  scrollbar-width: thin;
  scrollbar-color: currentColor transparent;
  max-height: 15em;
  position: relative;
  overflow-y: auto;
  mask-position: 0 0, 100% 0;
  mask-repeat: no-repeat;
}

.masked-overflow.no-overflow {
  padding-bottom: 0;
  mask-image: none;
}

.masked-overflow::-webkit-scrollbar {
  width: var(--scrollbar-width);
}

.masked-overflow::-webkit-scrollbar-thumb {
  background-color: currentColor;
  border-radius: 9999px;
}

.masked-overflow::-webkit-scrollbar-track {
  background: none;
}
